<div class="modal fade mymodal" id="employee-edit-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="employeeEditModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
