<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>
<script setup>
    import { onMounted, ref ,inject,watch } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import employees from '@/views/masters/employees.vue';
    useMeta({ title: 'Custom Table' });
import Multiselect from '@suadelabs/vue3-multiselect';
import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

     const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const showMessage = inject('showMessage')
    const showError = inject('showError')
    const formatNumber = inject('formatNumber')
    const formatDate = inject('formatDate')
    const printEmployeeList = inject('printEmployeeList')
    const tableShimmer = inject('tableShimmer');
    const loading_spinner = ref(false);
    const columns = ref(['Sno', 'name','department','salary','contract_date', 'status','actions']);
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 , 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'service_name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const company_id = ref('')

    const addEmployee = ref(false)    
    const employee = ref('')
    const ghrmEmployees = ref([])
    const getGhrmEmployees = async () => {
        loading_spinner.value = true;
        try {
            
            await getEmployees();
            
            const response = await axios.get(`https://ghrm.abooderp.com/api/employees/information?entity_id=${company_id.value}`);
            const allEmployees = response.data.data;
            
            const registeredUUIDs = employeesList.value.map(emp => emp.uuid);
            ghrmEmployees.value = allEmployees.filter(emp => 
                !registeredUUIDs.includes(emp.uuid)
            );
        } catch (error) {
            console.log(error)
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };

    const employeesList = ref([])

  const getEmployees = async () => {
        loading_spinner.value = true;
        try{
        const response = await axiosInstance.get(`employees?company_id=${company_id.value}`)
        employeesList.value = response.data.map((item,index)=>({
         ...item,
         Sno : index + 1,
         salary : formatNumber(item.salary),
         employee_salary : item.salary,
         name : item.name.toUpperCase()
        }))
        }catch(error){
            console.log(error)
            showError(error.message)
        }finally{
            loading_spinner.value = false;
        }
    };

    const salary = ref('')
    const contract_date = ref('')
    const registerEmployee = async() => {
      loading_spinner.value = true
        const payload = {
            uuid: employee.value.uuid,
            name: employee.value.full_name,
            company_id: company_id.value,
            salary: salary.value,
            contract_date: contract_date.value,
            department :employee.value.department_name
        }
        try {
            const response = await axiosInstance.post('employees', payload);
            showMessage('Employee Registered');
            addEmployee.value = !addEmployee.value;
            await getEmployees(); 
            await getGhrmEmployees(); 
            
            employee.value = '';
            salary.value = '';
            contract_date.value = '';
        } catch (error) {
            showError(error.message);
            console.log(error);
        }
    };

    const selectedEmployee = ref(null)
    const OpenEmployeeViewModal = async (row) => {
        selectedEmployee.value = row;
        await getEmployeeAttachments(row.id);
    };


    const showAttachmentForm = ref(false);
const attachmentDescription = ref('');
const employeeAttachment = ref(null);
const uploadingEmployeeAttachment = ref(false);

const handleEmployeeAttachmentUpload = (e) => {
    employeeAttachment.value = e.target.files[0];
};

const uploadEmployeeAttachment = async () => {
    uploadingEmployeeAttachment.value = true;

    const formData = new FormData();
    formData.append('employee_id', selectedEmployee.value.id);
    formData.append('remark', attachmentDescription.value);
    formData.append('attachment', employeeAttachment.value);

    try {
        const response = await axiosInstance.post(
            'employee-attachments',
            formData,
            { headers: { 'Content-Type': 'multipart/form-data' } }
        );
        
        showMessage('Employee attachment uploaded successfully!');
        await getReactiveEmployeeAttachments(selectedEmployee.value.id);
        cancelAttachmentUpload();
        
    } catch (error) {
        showError(error.message);
        console.log(error);
    } finally {
        uploadingEmployeeAttachment.value = false;
    }
};

const cancelAttachmentUpload = () => {
    showAttachmentForm.value = false;
    attachmentDescription.value = '';
    employeeAttachment.value = null;
    const fileInput = document.querySelector('input[type="file"]');
    if (fileInput) fileInput.value = '';
};


const employeeAttachments = ref([]);
const attachmentColumns = ref(['Sno', 'description', 'date', 'actions']);

const loadingAttachments = ref(false);
const getEmployeeAttachments = async (employeeId) => {
    loadingAttachments.value = true;
    try {
        const response = await axiosInstance.get(`employee-attachments?employee_id=${employeeId}`);
        employeeAttachments.value = response.data.map((item, index) => ({
            ...item,
            Sno: index + 1,
            attachment: process.env.VUE_APP_API_BASE_URL + '../../' + item.attachment,  
            date: formatDate(item.created_at),
            description: item.remark
        }));
    } catch (error) {
        showError(error.message);
        console.log(error);
    }finally{
        loadingAttachments.value = false; 
    }
};

 const getReactiveEmployeeAttachments = async (employeeId) => {
      const response = await axiosInstance.get(`employee-attachments?employee_id=${employeeId}`); // Load employee attachments from the API using employeeId as a parameter. This will fetch the attachments for the selected employee. You can also use the employeeId from the selectedEmployee.value.id. This is just an example. You can use any other method to get the employeeId.
        employeeAttachments.value = response.data.map((item, index) => ({
            ...item,
            Sno: index + 1,
            attachment: process.env.VUE_APP_API_BASE_URL + '../../' + item.attachment,  
            date: formatDate(item.created_at),
            description: item.remark
        }));

 }

  const getReactiveEmployees = async () => {
    const response = await axiosInstance.get(`employees?company_id=${company_id.value}`)
        employeesList.value = response.data.map((item,index)=>({
         ...item,
         Sno : index + 1,
         salary : formatNumber(item.salary),
         employee_salary : item.salary,
         name : item.name.toUpperCase()
        }))
  };

 const showConfirmation = inject('showConfirmation');
const DeleteEmployee = async (item) => {
  const result = await showConfirmation(
    'Delete Record',
    `Are you sure you want to delete ${item.name}?`,
    `employees/${item.id}`,
    'delete'
  )

  if (result?.refresh) {
    getReactiveEmployees()
  }
}

