 const employee_salary = ref('')
 const employee_contract_date = ref('')
 const updatingEmployee = ref(false)
 const updateEmployee = async () => {
    updatingEmployee.value = true;
    const payload = {
        name: employee_name.value,
        department: employee_department.value,
        salary: employee_salary.value,
        contract_date: employee_contract_date.value
    }
    try {
        const response = await axiosInstance.put(`employees/${selectedEmployee.value.id}`, payload);
        getReactiveEmployees()
        showMessage('Employee updated successfully!');
        document.getElementById('close_editing_employee').click();
    } catch (error) {
      console.log(error)
        showError(error.message);
    } finally {
        updatingEmployee.value = false;
    }
}
